    deep: true
  }
)

const componentClick = (item) => {
  console.log(': ', item)
  let column = {
    name: shortid.generate(),
    type: item.name,
    title: item.title,
    required: true
  }

  if (['select', 'radio', 'checkbox'].includes(item.type)) {
