<template>
  <div class="ace-container">
    <!-- id ref  DOM  -->
    <div class="ace-editor" ref="ace"></div>
  </div>
</template>

<script>
import ace from 'ace-builds'
/* webpackjs
     ace.config.set('basePath', 'path...')jsURL
   */
//import 'ace-builds/webpack-resolver'

//import 'ace-builds/src-min-noconflict/theme-monokai' // 
import 'ace-builds/src-min-noconflict/theme-sqlserver' // 
import 'ace-builds/src-min-noconflict/mode-javascript' // 
import 'ace-builds/src-min-noconflict/mode-json' //
import 'ace-builds/src-min-noconflict/mode-css' //
import 'ace-builds/src-min-noconflict/ext-language_tools'

export default {
  name: 'CodeEditor',
  props: {
    modelValue: {
      type: String
      //required: true
    },
    readonly: {
      type: Boolean,
      default: false
    },
    mode: {
      type: String,
      default: 'javascript'
    },
    userWorker: {
      //
      type: Boolean,
      default: true
    }
  },
  emits: ['update:modelValue'],
  mounted() {
    //ace.config.set('basePath', 'https://ks3-cn-beijing.ksyun.com/vform2021/ace')
    ace.config.set('basePath', 'https://ks3-cn-beijing.ksyun.com/vform2021/ace-mini')

    this.addAutoCompletion(ace) //

    this.aceEditor = ace.edit(this.$refs.ace, {
      maxLines: 20, // 
      minLines: 5, // 
      fontSize: 12, // 
      theme: this.themePath, // 
      mode: this.modePath, // 
      tabSize: 2, // 2
      readOnly: this.readonly,
      highlightActiveLine: true,
      value: this.codeValue
    })

    this.aceEditor.setOptions({
      enableBasicAutocompletion: true,
      enableSnippets: true, // 
      enableLiveAutocompletion: true // 
    })

    if (this.mode === 'json') {
      this.setJsonMode()
    } else if (this.mode === 'css') {
      this.setCssMode()
    }

    if (!this.userWorker) {
      this.aceEditor.getSession().setUseWorker(false)
    }

    //
    this.aceEditor.getSession().on('change', (ev) => {
      this.$emit('update:modelValue', this.aceEditor.getValue())
    })
  },
  data() {
    return {
      aceEditor: null,
      themePath: 'ace/theme/sqlserver', //  webpack-resolver
      modePath: 'ace/mode/javascript', // 
      codeValue: this.modelValue
    }
  },
  watch: {
    //
  },
  methods: {
    addAutoCompletion(ace) {
      let acData = [
        { meta: 'VForm API', caption: 'getWidgetRef', value: 'getWidgetRef()', score: 1 },
        { meta: 'VForm API', caption: 'getFormRef', value: 'getFormRef()', score: 1 }
        //TODO: 
      ]
      let langTools = ace.require('ace/ext/language_tools')
      langTools.addCompleter({
        getCompletions: function (editor, session, pos, prefix, callback) {
          if (prefix.length === 0) {
            return callback(null, [])
          } else {
            return callback(null, acData)
          }
        }
      })
    },

    setJsonMode() {
      this.aceEditor.getSession().setMode('ace/mode/json')
    },

    setCssMode() {
      this.aceEditor.getSession().setMode('ace/mode/css')
    },

    getEditorAnnotations() {
      return this.aceEditor.getSession().getAnnotations()
    },

    setValue(newValue) {
      this.aceEditor.getSession().setValue(newValue)
    }
  }
}
</script>

<style lang="scss" scoped>
.ace-editor {
  min-height: 300px;
}
</style>
